@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

/* SIDER MAIN LAYOUT */
.ant-layout-sider {
  position: fixed !important;
  height: 100vh;
  z-index: 88;
  box-shadow: 1px 0px 5px 0px rgba(176, 176, 176, 0.5) !important;
}
.ant-layout-header {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 50px;
  line-height: 50px;
  box-shadow: 0px 1px 5px 0px #c9c9c9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ant-layout-sider-children {
  padding-top: 55px !important;
}
.ant-layout-has-sider .ant-layout-content {
  position: fixed !important;
  left: 200px;
  top: 50px;
  width: calc(100% - 200px);
  height: calc(100% - 80px);
  padding-bottom: 50px;
  overflow: hidden;
  overflow-y: auto;
  transition: all 0.5s;
}

.main-content > * {
  height: 100%;
  margin-top: 10px;
  padding-left: 30px;
}
.main__toggle {
  left: 80px !important;
  width: calc(100% - 80px) !important;
}

.layout-header .ant-btn {
  display: flex;
  align-items: center;
  background: none;
  font-size: 20px;
  font-weight: 600;
  width: 50px;
}
.layout-header .ant-btn:hover,
.layout-header .ant-btn:focus {
  color: #0cc77f !important;
  background: transparent !important;
}
.ant-btn.ant-btn-icon-only .anticon {
  padding: 0px 10px;
  font-size: 20px !important;
}
.ant-menu-light .ant-menu-item-selected,
.ant-menu-light > .ant-menu .ant-menu-item-selected {
  background-color: transparent !important;
}
.ant-menu-item {
  margin: 4px 0px 8px 0px !important;
}
.ant-menu-inline-collapsed > .ant-menu-item,
.ant-menu-inline-collapsed
  > .ant-menu-item-group
  > .ant-menu-item-group-list
  > .ant-menu-item,
.ant-menu-inline-collapsed
  > .ant-menu-item-group
  > .ant-menu-item-group-list
  > .ant-menu-submenu
  > .ant-menu-submenu-title,
.ant-menu-inline-collapsed > .ant-menu-submenu > .ant-menu-submenu-title {
  padding-inline: calc(50% - 8px - 0px) !important;
}
.ant-menu-item-selected {
  transform: 0s !important;
}
.ant-menu-item:hover,
.ant-menu-item a:hover {
  background: transparent !important;
  color: #1890ff !important;
}

.ant-menu-item-selected {
  color: #1890ff !important;
}
.ant-menu-item-selected:nth-child(1),
.ant-menu-item-selected:nth-child(4),
.ant-menu-item-selected:nth-child(4),
.ant-menu-item-selected:nth-child(6),
.ant-menu-item-selected:nth-child(6) {
  color: #ffffff !important;
  margin-left: 10px;
  background-color: #0cc77f !important;
  border-left: 5px solid #353535;
  border-right: 2px solid #1890ff;
  font-weight: bold;
}
.ant-menu-item-selected:nth-child(1):hover,
.ant-menu-item-selected:nth-child(1) a:hover,
.ant-menu-item-selected:nth-child(4):hover,
.ant-menu-item-selected:nth-child(4) a:hover,
.ant-menu-item-selected:nth-child(6):hover,
.ant-menu-item-selected:nth-child(6) a:hover {
  color: #ffffff !important;
}
.ant-menu-inline.ant-menu-root .ant-menu-item,
.ant-menu-inline.ant-menu-root .ant-menu-submenu-title {
  transition: 0s !important ;
}
.ant-menu .ant-menu-item,
.ant-menu .ant-menu-submenu,
.ant-menu .ant-menu-submenu-title {
  border-radius: 0px;
}
/* .ant-menu .ant-menu-item, .ant-menu .ant-menu-submenu-title {
  transition: 0s !important ;
}
.ant-menu {
  transition: 0s !important ;
} */
.ant-menu-inline .ant-menu-item,
.ant-menu-vertical .ant-menu-item,
.ant-menu-inline .ant-menu-submenu-title,
.ant-menu-vertical .ant-menu-submenu-title {
  width: calc(100% - 0px) !important;
}
/* MAIN SETTING */
.main__layout__setting .ant-layout-sider-children {
  padding-top: 28px !important;
}
.main__layout__setting .ant-menu-item {
  border-bottom: 1px solid rgb(221, 221, 221);
}
.main__layout__setting .ant-menu-item-selected {
  color: #1890ff !important;
  background-color: #e6f7ff !important;
  border-left: 0px;
  border-right: 0px;
  font-weight: normal !important;
}
.main__layout__setting .ant-menu-item-selected:hover,
.main__layout__setting .ant-menu-item-selected a:hover {
  color: #1890ff !important;
}
.main__layout__setting .ant-menu-item {
  padding-left: 17px !important;
}

.ant-menu-light.ant-menu-root.ant-menu-inline,
.ant-menu-light > .ant-menu.ant-menu-root.ant-menu-inline,
.ant-menu-light.ant-menu-root.ant-menu-vertical,
.ant-menu-light > .ant-menu.ant-menu-root.ant-menu-vertical {
  border-inline-end: 0px ; 
}
.main__layout__setting .ant-layout-sider {
  position: unset !important;
  height: 37vh;
  box-shadow: 0px;
}
.main__layout__setting .ant-layout-has-sider .ant-layout-content {
  position: unset !important;
  width: 100%;
}
button, [type='button'], [type='reset'], [type='submit'] {
  background-color: #e5e7eb; 
}
